<template>
  <div>
    <mytil>收藏页</mytil>
    <div class="list_box">
      <div class="list_box">
        <item v-for="item in list" :key="item.bookId" :item="item" :isShowCollect='false' @mydel='fn'></item>
      </div>
    </div>
  </div>
</template>

<script>
import title from "../../common/title.vue";
import item from "../list/item.vue"
export default {
  components: {
    mytil: title,
    item
  },
  computed: {
    list() {
      // 获取vuex中的collectData
      return this.$store.state.collectData;
    }
  },
  // 业务逻辑定义在父组件的函数中，子组件只负责触发父组件的函数，并且传一些参数
  methods: {
    fn(item){
      console.log(item);
      this.$store.commit('removeCollect',item)
    }
  }
};

</script>

<style lang="less" scoped>
.list_box {
  margin: 1.2rem 0;
}
</style>